"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";

type Item = { label: string; href: string };

const items: Item[] = [
  { label: "Inicio", href: "/athlete" },
  { label: "Reservar", href: "/athlete/reservas" },
  { label: "WOD", href: "/athlete/wod" },
  { label: "Pagos", href: "/athlete/pagos" },
  { label: "Perfil", href: "/athlete/perfil" },
];

export default function BottomNav() {
  const pathname = usePathname();

  return (
    <nav className="fixed bottom-0 left-0 right-0 z-20 border-t border-gray-200 bg-white">
      <div className="mx-auto grid max-w-md grid-cols-5">
        {items.map((it) => {
          const active = pathname === it.href;
          return (
            <Link
              key={it.href}
              href={it.href}
              className={`px-2 py-3 text-center text-xs ${
                active ? "font-semibold text-gray-900" : "text-gray-500"
              }`}
            >
              {it.label}
            </Link>
          );
        })}
      </div>
    </nav>
  );
}
